/** @format */

* {
	margin: 0;
	padding: 0;
}

.padding-y {
	padding: 3% 0;
}

.animation {
	opacity: 0;
	transition: all 0.5s ease-out;
	animation: about 0.6s ease-out 0.75s;
}

@keyframes about {
	0% {
		opacity: 0;
		transform: translateY(-500px);
	}
	80% {
		opacity: 1;
		transform: translateY(30px);
	}
	100% {
		opacity: 1;
		transform: translate(0);
	}
}

.about-header {
	text-align: center;
	margin-top: 8%;
	color: #343a40;
	font-weight: bold;
	font-family: 'eurostile', sans-serif;
	font-size: 45px;
}

.about-dis {
	text-align: center;
	font-size: 25px;
	color: #222222;
}

.about-img {
	margin-top: 5%;
	text-align: center;
}

.about-me {
	margin-top: 8%;
	background-color: #138496;
	padding: 2%;
	text-align: center;
	/* width: 100vw; */
}

.about-me-content {
	width: 70%;
	margin: 0 auto;
	margin-top: 5%;
}

.about-me .about-me-content h1 {
	font-family: 'eurostile', sans-serif;
	font-weight: bold;
	color: #ffffff;
	margin-bottom: 2%;
	font-size: 45px;
}

.about-me .about-me-content p {
	color: #ffffff;
	width: 70%;
	margin: auto;
	margin-bottom: 2%;
}

.about-me .about-me-content p span {
	font-weight: bold;
	display: block;
	text-align: left;
	color: #222222;
	text-decoration: underline;
	margin-bottom: 2%;
}

.about-cv {
	display: inline-block;
	width: 20%;
	font-size: large;
	height: 45px;
	border-radius: 50px;
	margin: 3% auto;
	background-color: #fff;
	background-color: #fff;
	color: #777;
	animation: moveInBottom 0.5s ease-out 0.75s;
	animation-fill-mode: backwards;
	position: relative;
}

#about-cv {
	width: 100%;
	text-decoration: none;
	font-weight: bold;
	transition: all 0.2s;
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
}

.about-cv:hover #about-cv {
	color: #b3006e;
}

.about-cv:hover {
	transform: translateY(-3px);
	box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
}

.about-cv:active {
	transform: translateY(-1px);
	box-shadow: 0 5px 10px rgba(0, 0, 0, 0.2);
}

.about-cv::after {
	content: '';
	display: inline-block;
	width: 100%;
	height: 100%;
	position: absolute;
	top: 0;
	left: 0;
	z-index: -1;
	border-radius: 50px;
	transition: all 0.4s;
}

.about-cv::after {
	background-color: #fff;
}

.about-cv:hover::after {
	transform: scaleX(1.4) scaleY(1.6);
	opacity: 0;
}

@keyframes moveInBottom {
	0% {
		opacity: 0;
		transform: translateY(50px);
	}

	100% {
		opacity: 1;
		transform: translate(0);
	}
}

@media only screen and (max-width: 768px) {
	.about-header {
		font-size: 30px;
		margin-top: 25%;
	}

	.about-dis {
		font-size: 18px;
		width: 95%;
	}

	.about-img {
		margin-top: 10%;
	}

	.about-me {
		margin-top: 33%;
	}

	.about-me-content {
		margin-top: 8%;
		width: 95%;
	}

	.about-me .about-me-content h1 {
		font-size: 30px;
	}

	.about-me .about-me-content p {
		width: 100%;
		margin-bottom: 6%;
	}
	.about-cv {
		width: 50%;
	}
}
