/** @format */

Navbar > .container {
	width: auto;
}

.container a {
	color: #fff !important;
}

.container a:hover {
	color: #fff !important;
	text-decoration: none;
}

.container .head-header a {
	font-size: 18px;
}

.container .head-header a:hover {
	color: aqua !important;
}

#navLinkHome,
#navLinkProject,
.container a,
.drop {
	width: 100%;
	margin: auto 8%;
	color: #fff !important;
	font-size: 16px;
	/* margin-left: 3%; */
	text-align: center;
	transition: 0.5s;
}

#navLinkHome:hover,
#navLinkProject:hover {
	color: aqua !important;
	text-decoration: none;
	cursor: pointer;
}

#navLinkContact {
	width: 90px;
	padding: 13px;
	color: #fff !important;
	margin: auto;
	text-align: center;
	border: 2px solid #18a4b9;
	border-radius: 15px;
	box-sizing: border-box;
	font-size: 85%;
	font-weight: bold;
	transition: 0.5s;
}

#navLinkContact:hover {
	color: #fff !important;
	background-color: #18a4b9;
	text-decoration: none;
	cursor: pointer;
}

#navLinkHome.active,
#navLinkProject.active,
.container a.active {
	color: #28bdd4 !important;
}

#navLinkContact.active {
	background-color: #18a4b9;
	color: #fff !important;
}

.custom-switch {
	width: 80%;
	padding: 2% auto 2% auto;
	margin: 2% auto 2% auto;
}

.cv {
	display: flex;
	justify-content: center;
	align-items: center;
	border-radius: 15px;
	width: 80%;
	height: 38px;
	margin: auto;
	background-color: #18a4b9;
}

#cv {
	width: 100%;
}

.cv:hover {
	transform: translateY(-3px);
	box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
}

.cv:active {
	transform: translateY(-1px);
	box-shadow: 0 5px 10px rgba(0, 0, 0, 0.2);
}

@media only screen and (max-width: 991px) {
	#navLinkHome,
	#navLinkProject,
	.container a,
	.drop {
		width: 100%;
		margin: 2% auto;
	}

	#navLinkContact {
		width: 50%;
		padding: 2%;
		margin: 2% auto;
		font-size: 95%;
	}

	.custom-switch {
		width: 50%;
		padding: 2% auto 2% auto;
		margin: 2% auto 2% auto;
	}

	.cv {
		width: 60%;
	}
}
